<template>
  <div class="div-container">

    <!-- Select Box -->
    <div class="div-select_parent">
      <div class="div-select_child">
        <v-select
          class="select-sort"
          label="Sort by"
          :items="sortOptions"
          @change="sortBy"
          outlined
          dense
          :menu-props="{ bottom: true, offsetY: true }"
        ></v-select>
      </div>
    </div>
    <!-- Select Box End -->

    <!-- Product List -->
    <div>
      <ul class="ul-clothing_list">
        <li class="li-clothing_list_item" v-for="i in clothingToShow" :key="i">
          <div class="div-clothing_list_item_wrapper">
            <v-img :src="require(`@/assets/${clothing[i - 1].url}.webp`)" />
            <div align="left">
              <div class="div-clothing_name">{{clothing[i - 1].name}}</div>
              <div class="div-clothing_price">{{clothing[i - 1].price}}₩</div>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <!-- Product List End -->

    <!-- Load More -->
    <div v-if="clothingToShow < totalClothing" class="div-load_more_btn_wrapper" align="center">
      <v-btn class="btn-load_more" elevation="0" @click="loadMore">Load More</v-btn>
    </div>
    <!-- Load More End -->

  </div>
</template>

<script>
export default {
  data() {
    return {
      clothing: [],
      totalClothing: 0,
      clothingToShow: 0,
      loadDataCount: 16,
      sortOptions: [
        'Newest',
        'Price (low to high)',
        'Price (high to low)',
        'Name A-Z',
        'Name Z-A'
      ]
    }
  },

  mounted() {
    this.getData();
  },

  methods: {
    // 상품 데이터 가져오기
    getData() {
      this.$axios.get('/test').then((res) => {
        res.data.forEach((element) => {
          this.clothing.push({
            name: element.name,
            price: element.price.toLocaleString("ko-KR"),
            url: element.url,
            register_date: element.register_date
          });
        });

        this.totalClothing = this.clothing.length;
        this.clothingToShow = Math.min(this.loadDataCount, this.totalClothing);
        console.log(this.clothing);
      });
    },

    // 상품 데이터 추가 로드
    loadMore() {
      this.clothingToShow += Math.min(this.loadDataCount, this.totalClothing - this.clothingToShow);
    },

    // 상품 리스트 정렬
    sortBy(option) {
      if (option == this.sortOptions[0]) {
        this.clothing.sort((a, b) => {
          return new Date(b.register_date).getTime() - new Date(a.register_date).getTime();
        });
      } else if (option == this.sortOptions[1]) {
        this.clothing.sort((a, b) => {
          return Number(a.price.replace(',', '')) - Number(b.price.replace(',', ''));
        });
      } else if (option == this.sortOptions[2]) {
        this.clothing.sort((a, b) => {
          return Number(b.price.replace(',', '')) - Number(a.price.replace(',', ''));
        });
      } else if (option == this.sortOptions[3]) {
        this.clothing.sort((a, b) => {
          return a.name.localeCompare(b.name);
        });
      } else if (option == this.sortOptions[4]) {
        this.clothing.sort((a, b) => {
          return b.name.localeCompare(a.name);
        });
      }
    }
  },
};
</script>

<style>
.div-container {
  padding: 0 15px 0 15px;
}

.div-select_parent {
  display: flex;
  justify-content: right;
  margin: 20px 25px 20px 0;
}

.div-select_child {
  width: 200px;
  height: 40px;
}

.select-sort {
  border-radius: 0px !important;
}

.select-sort input {
  cursor: pointer;
}

.my-app.v-application .primary--text {
  color: black !important;
}

.v-text-field--outlined fieldset {
  border: black 1px solid !important;
}

.ul-clothing_list {
  list-style: none;
}

.li-clothing_list_item {
  float: left;
  margin: 0 20px 80px 0;
}

.div-clothing_list_item_wrapper {
  width: 345px;
  height: 345px;
}

.div-clothing_name {
  font-size: 17px;
  line-height: 23.8px;
  margin-top: 5px;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.div-clothing_price {
  font-size: 16px;
  line-height: 16px;
  color: #666666;
  margin-top: 5px;
}

.div-load_more_btn_wrapper {
  margin: 40px 0 40px 0;
  clear: both;
}

.btn-load_more {
  background-color: black !important;
  color: white !important;
  border-radius: 0px !important;
  font-size: 17px;
  line-height: 23.8px;
  text-align: center;
  word-spacing: 0px;
  height: 43.8px;
  width: 316px;
  padding: 10px 20px 10px 20px;
}
</style>